<script>
import HeaderBox from "@/components/HeaderBox.vue";
import PhoneHeader from "@/components/PhoneHeader.vue";
import PhoneHeaderBox from "@/components/PhoneHeaderBox.vue";

import moment from "moment";
import api from "@/api/apis.js";

export default {
  components: {
    HeaderBox,
    PhoneHeader,
    PhoneHeaderBox,
  },
  data() {
    return {
      disBeforeTime: {
        disabledDate(date) {
          return date.getTime() < Date.now() - 24 * 60 * 60 * 1000;
        },
      },
      titleImg: require("@/assets/images/headerBox/reservation.png"),
      titleName: "",
      mainPageName: "",
      childrenPage: [
        {
          imgURL: "",
          imgURL_hover: "",
          path: "carGroup",
          pageName: "",
        },
        {
          imgURL: "",
          imgURL_hover: "",
          path: "drts",
          pageName: "DRTS",
        },
      ],
      selfPayUserId: "",
      userProfile: [],
      /* tab */
      tabTitle: [
        {
          value: "carGroup",
          name: "",
        },
        {
          value: "drts",
          name: "DRTS",
        },
        // {
        //   value: "bus",
        //   name: "",
        // },
      ],
      nowChoose: "carGroup",
      /*  */
      carGroupList: {
        // thiId: 0,
        orderStatus: "",
        reserveName: window.localStorage.getItem("userName"),
        town: "SSTW",
        village: "",
        userType: "",
        name: window.localStorage.getItem("userName"),
        reserveDate: "",
        fromTime: "",
        fromAddr: "",
        fromLng: "",
        fromLat: "",
        toTime: "",
        toAddr: "",
        toLng: "",
        toLat: "",
        hasTransfer: "",
        transferTraffic: "",
        transferOperator: "",
        transferTraffic2: "",
        transferOperator2: "",
        transferTraffic3: "",
        transferOperator3: "",
        transferPurpose: "",
        stationLineId: "",
        stationType: "",
        contactPhone: "",
        carpoolNum: 0,
        birthday: "",
      },
      rules_share: {
        reserveName: [
          { required: true, message: "", tigger: "blur" },
        ],
        contactPhone: [
          { required: true, message: "", tigger: "blur" },
        ],
        town: [{ required: true, message: "", tigger: "change" }],
        village: [{ required: true, message: "", tigger: "blur" }],
        userType: [{ required: true, message: "", tigger: "blur" }],
        name: [{ required: true, message: "", tigger: "change" }],
        birthday: [{ required: true, message: "", tigger: "change" }],
        carpoolNum: [{ required: true, message: "" }],
        reserveDate: [
          { required: true, message: "", tigger: "change" },
        ],
        fromTime: [
          { required: true, message: "", tigger: "change" },
        ],
        fromAddr: [
          { required: true, message: "", tigger: "change" },
        ],
        fromLng: [{ required: true, message: "" }],
        fromLat: [{ required: true, message: "" }],
        toTime: [
          { required: true, message: "", tigger: "change" },
        ],
        toAddr: [{ required: true, message: "", tigger: "blur" }],
        toLng: [{ required: true, message: "" }],
        toLat: [{ required: true, message: "" }],
        hasTransfer: [
          { required: true, message: "", tigger: "change" },
        ],
        transferTraffic: [
          { required: true, message: "1", tigger: "change" },
        ],
        transferOperator: [
          { required: true, message: "1", tigger: "change" },
        ],
        transferTraffic2: [
          { required: true, message: "2", tigger: "change" },
        ],
        transferOperator2: [
          { required: true, message: "2", tigger: "change" },
        ],
        transferPurpose: [
          { required: true, message: "", tigger: "change" },
        ],
      },
      /* DRTS */
      drtsLines: [],
      drtsLinesIds: [],
      drtsStation: [],
      alldrtsStation: [],
      drtsList: {
        name: window.localStorage.getItem("userName"),
        town: "SSTW",
        village: "",
        userType: "",
        reserveDate: "",
        fromTime: "",
        fromAddr: "",
        toAddr: "",
        hasTransfer: "",
        transferTraffic: "",
        transferOperator: "",
        transferPurpose: "",
        stationLineId: "",
        stationType: "DRTS",
      },
      rules_drts: {
        name: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        town: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        village: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        userType: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        reserveDate: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        fromTime: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        hasTransfer: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        transferTraffic: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        transferOperator: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        transferPurpose: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        stationLineId: [
          {
            required: true,
            message: "",
            trigger: "change",
          },
        ],
        fromAddr: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        toAddr: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },

      disAfterDate: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
      },
    };
  },
  computed: {
    timeStartTime_carGroup() {
      let time;
      if (
        this.carGroupList.reserveDate !==
        moment(new Date()).format("YYYY-MM-DD")
      ) {
        time = "06:00";
      } else {
        let nowHr = moment().format("HH");
        let nowMin =
          (Math.floor(moment().format("hh:mm").split(":")[1] / 10) + 1) * 10;

        if (nowMin == 60) {
          nowMin = "00";
          nowHr++;
        }

        time = `${nowHr}:${nowMin}`;
      }
      return time;
    },
    timeStartTime_drts() {
      let time;
      if (
        this.drtsList.reserveDate !== moment(new Date()).format("YYYY-MM-DD")
      ) {
        time = "06:00";
      } else {
        let nowHr = moment().format("HH");
        let nowMin =
          (Math.floor(moment().format("hh:mm").split(":")[1] / 10) + 1) * 10;

        if (nowMin == 60) {
          nowMin = "00";
          nowHr++;
        }

        time = `${nowHr}:${nowMin}`;
      }
      return time;
    },
  },
  methods: {
    changeTab(data) {
      this.nowChoose = data.value;
    },
    async reload() {
      const listQuery = {
        page: 1,
        limit: 999,
      };
      await api.GetUserInfo(listQuery).then((res) => {
        this.userProfile = res.data.data.filter(
          (resp) => resp.id === window.localStorage.getItem("userID")
        )[0];
        this.$store.dispatch("loadingHandler", false);
      });
    },
    // 
    /*  */
    reservation_carGroup() {
      this.$refs["dataForm_share"].validate((valid) => {
        if (valid) {
          this.$store.dispatch("loadingHandler", true);
          api.Orders(this.carGroupList).then((res) => {
            if (res.data.code == 200) {
              this.$message({
                type: "success",
                message: "!",
              });
              this.$router.push({ name: "memberInfo" });
            } else {
              this.$message({
                type: "warning",
                message: res.data.message,
              });
            }
            this.$store.dispatch("loadingHandler", false);
          });
        }
      });
    },
    /*  */
    // getFromGeo(str) {
    //   this.$store.dispatch("loadingHandler", true);
    //   api
    //     .GetGeo({ _addr: str })
    //     .then((res) => {
    //       this.carGroupList.fromLng = res.data.result.lon;
    //       this.carGroupList.fromLat = res.data.result.lat;
    //       this.$store.dispatch("loadingHandler", false);
    //     })
    //     .catch((err) => {
    //       this.$store.dispatch("loadingHandler", false);
    //     });
    // },
    /*  */
    getToGeo(str) {
      this.$store.dispatch("loadingHandler", true);
      api
        .GetGeo({ _addr: str })
        .then((res) => {
          this.carGroupList.toLng = res.data.result.lon;
          this.carGroupList.toLat = res.data.result.lat;
          this.$store.dispatch("loadingHandler", false);
        })
        .catch((err) => {
          this.$store.dispatch("loadingHandler", false);
        });
    },

    // DRTS
    /*  */
    getLines() {
      const lineQuery = {
        page: 1,
        limit: 999,
        key: undefined,
      };
      api.busStationLines(lineQuery).then((res) => {
        this.drtsLines = res.data.data;
      });
    },
    getStationLines(val) {
      this.$store.dispatch("loadingHandler", true);
      /* ID */
      const linesData = this.drtsLines.filter((res) => res.name == val)[0].id;
      /* () */
      const getDayOfWeek = moment(this.drtsList.reserveDate)
        .weekday()
        .toString();

      /*  */
      api.busLinesData({ id: linesData }).then((res) => {
        let getWorkWeek = res.data.result.workWeek.split(",");

        /*    */
        if (
          res.data.result.workWeek == "" ||
          !getWorkWeek?.includes(getDayOfWeek)
        ) {
          this.drtsStation = null;
        } else {
          this.drtsLinesIds = res.data.result.assignLineStations;
          this.drtsStation = this.alldrtsStation.filter((item) =>
            this.drtsLinesIds.includes(item.id)
          );
        }
        this.$store.dispatch("loadingHandler", false);
      });
    },
    /*  */
    getStations() {
      const busQuery = {
        page: 1,
        limit: 999,
        key: undefined,
      };
      api.busStations(busQuery).then((res) => {
        this.alldrtsStation = res.data.data;
      });
    },
    /*  */
    getDate(val) {
      if (!!this.drtsList.stationLineId) {
        this.getStationLines(this.drtsList.stationLineId);
      }
    },
    /*  */
    reservation_drts() {
      this.$refs["dataForm_drts"].validate((valid) => {
        if (valid) {
          this.$store.dispatch("loadingHandler", true);
          api.Orders(this.drtsList).then((res) => {
            if (res.data.code == 200) {
              this.$message({
                type: "success",
                message: "!",
              });
              this.$router.push({ name: "memberInfo" });
            } else {
              this.$message({
                type: "warning",
                message: res.data.message,
              });
            }
            this.$store.dispatch("loadingHandler", false);
          });
        }
      });
    },
  },
  mounted() {
    this.$store.dispatch("loadingHandler", true);
    this.reload();
    this.getLines();
    this.getStations();
  },
};
</script>

